.member-wrap {
    background: #f4f4f4;

	@media (max-width: $device-sm) {
		padding-top:$header-mobile-height;
		padding-bottom: 0;
	}

    .board {
        border: 0;
        margin-top: 0;
        min-height: 518px;

        @media (max-width: $device-sm) {
			margin: 0 -15px;
        }

        .sub-title {
            margin: 20px 0;

            &:before {
                content: '';
                display: inline-block;
                width:4px;
                height: 20px;
                margin-right: 10px;
                background-color: #333;
                vertical-align: sub;
            }
        }
    }

    thead th{
        background-color: #EEEEEE;
    }

    .table-btns {
        margin-bottom: 10px;

        .btn {
            margin-right: 5px;
        }
    }
}

.page-head, .page-search {
    border-bottom: 1px solid #eaeaea;
    margin: 0 -20px 20px -20px;
    padding: 0 20px;
}
.page-head h1 {
    margin-top: 0;
    margin-bottom: 20px;
}

.page-tab {
    border: 0;

    > li {
        border: 0;

        &:hover {
            border-bottom:3px solid rgba(19,129,132,.5);
        }

        &.active {
            border-bottom:3px solid #138184;
        }

        > a,
        > a:hover,
        > a:focus{
            color:#999;
            font-weight:bold;
            border:0;
            background-color:transparent;
            padding:10px 20px;
            margin:0;
            position: relative;
        }
    }

    > li,
    > li.active > a,
    > li.active > a:hover,
    > li.active > a:focus{
        color:#138184;
        border:0 ;
    }
}

.page-search {
    [class*=col-]  .btn-primary{
        margin-top: 24px;
        width: 100%;
        @media (max-width: $device-sm) {
            margin-top: 0;
            margin-bottom: 20px;
            width: auto;
        }
    }

    .form-btns {
        margin-bottom: 20px;
    }
}

$step-bar-height: 50px;
ul.review-step {
    height: $step-bar-height;
    margin: 40px 0;
    border-radius: $step-bar-height;
    background-color: $bg-grey-light;
    display: flex;
    align-items: center;
    justify-content: space-around;
    overflow: hidden;

    > li {
        color: $text-light;
        width:25%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;

        &.active {
            border-radius: $step-bar-height;
            background-color: $brand-info;
            color: #fff;
        }

        &.pass {
            background: #94DFE5;
            color: #fff;

            &:after {
                content: '';
                display: block;
                width: 20px;
                height: 100%;
                background: #94DFE5;
				position: absolute;
				top: 0;
                left: 100%;
            }
        }
    }

    & + .form-btns {
        margin: 40px 0;
        text-align: center;
    }
}

.cache-time {
    border-bottom: 1px solid #eee;
    padding-bottom: 15px;
    margin-bottom: 15px;

    .edit-time {
        border-bottom: 1px dashed $brand-primary;
        padding: 0 5px;
        font-weight: bold;
        cursor: pointer;
    }

    .edit-time-input {
        border-radius: 4px;
        border: 1px solid #ddd;
        padding: 5px;
        width: 50px;
    }

    a {
        font-weight: bold;
        text-decoration: none;
        margin-left: 4px;
    }
}
